.stars {
  display: flex;
  width: 96px;
  justify-content: space-between;

  &__star {
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    height: 16px;
    width: 16px;
  }

  &--N :nth-child(-n + 4) {
    background-image: url(../images/star-active.svg);
  }
}
